.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html,body {margin : 0; padding : 0;}
h1,h2,h3,h4,h5,div,p,a,span {margin : 0; padding : 0;}
.black-nav {background-color: #111; color : #fff; display: flex; padding : 20px;}
.list {text-align: left; padding : 20px; border-bottom : 1px solid #ddd; position: relative;}
.list h5 {cursor: pointer;}
.list h5 span {cursor: pointer; display: inline-block;}
.list p {padding-top : 5px; font-size: 12px; color : #555;}
.list .delete {display: inline-block; background-color :tomato; font-size: 14px; padding : 5px 15px; position: absolute; right : 20px; top : 50%; transform: translate(0,-50%); cursor: pointer; font-weight: 600;}

.hw_btn {display: inline-block; max-width : 200px; width :calc(50% - 15px); height: 50px; line-height: 50px; background-color: #333; color : #fff; text-align: center; margin : 50px 5px; cursor: pointer;}

.modal {background-color: rgba(0,0,0,0.4); width :100%; height: 100%; position: fixed; top :0; left : 0; z-index: 1;;}
.modal .box {max-width : 500px; width : 90%; min-height: 500px; background-color: #fff;position: absolute; left : 50%; top : 50%; transform: translate(-50%,-50%); text-align: left;;}
.modal h3 {padding : 20px 20px 0 20px;}
.modal h3 + p {padding : 0 20px; font-size: 14px; color : #555;}
.modal p + p {padding : 30px 20px;}
.modal a {text-decoration: unset; color : #333; font-size: 14px; position: absolute; right : 20px; top : 22px;}

.line {width : 100%; height: 50px; border-bottom : 1px solid red; display: block; margin-bottom : 50px;}

.inputArea {background-color :whitesmoke;max-width: 800px; margin : 0 auto; padding : 50px 5%; box-sizing: border-box;}
.inputArea >div{display: flex; align-items: center;;}
.inputArea >div input {height: 40px; flex : 1; border : 0; padding : 0 10px; outline: none;;}
.inputArea >div button {width : 100px; height: 40px; background-color: #333; color : #fff; border : 0; margin-left : 10px;}